body{
	margin: 0;
	padding: 0;
	background-color:#0f0e13;
  background-image: 
    radial-gradient(at 40% 70%, rgb(39, 59, 72) 0, transparent 60%), 
    radial-gradient(at 50% 0%, rgb(52, 82, 170) 0, transparent 30%), 
    radial-gradient(at 100% 30%, hsla(339,39%,30%,1) 0, transparent 50%);

	
    background-size: cover; 
    background-attachment: fixed;   
}
.main{
    position: absolute;
    width: 80%;
    height: 80%;
    left: 10%;
    top: 10%;
    display: inline-flex;
    flex-wrap: wrap;
}

#nav{
	width: 100%;
    height: 55px;
    background-color: #ced2f2;
    position: fixed;
    z-index: 2;
}
#options{
	font-size: 18px;
    position: absolute;
    left: 0px;
    text-align: center;


}
#options li{
    display: inline-flex;
    margin-right: 30px;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
    border-bottom: double;

}
#options li:hover{
	transform: scale(1.1);
	color:#c37de3;
}

#options li a{
	text-decoration: none;
  color: #000000
}

.form{
    background-image: radial-gradient(at 0% 10%, rgb(41, 45, 49) 0, transparent 50%),
     radial-gradient(at 80% 50%, rgb(175, 67, 132) 0, transparent 50%),
      radial-gradient(at 60% 90%, rgb(53, 50, 137) 0, transparent 70%);
    width: 26%;
    position: relative;
    height: 70%;
    overflow: hidden;
    box-shadow: 2px 2px 10px saddlebrown;
    padding: 5px;
    margin: 10px;
    top: 15%;
    left: 19%;
}
   
   
    
   
	



#option{
	float: right;
    font-size: 20px;
    position: relative;
    top: 1rem;
    color: #4caf50;
    text-decoration: none;
    right: 1rem;
}

.form h1,.form-two h1{
	position: relative;
	text-decoration: underline;
	font-size: 30px;
	padding: 13px;
	color:transparent;
	
	font-family: 'Merriweather Sans', sans-serif;
	background-image: linear-gradient(to right,#d12c73,#dea66a,#dea66a,#63bf4e,#cc2129,#dec468,#6897ed);
	-webkit-background-clip:text;
	animation: animate 20s linear infinite;
	background-size: 450%;
    text-align: center;
    font-weight: 900;

}



@keyframes animate{

0%{
	background-position: 0% 100%
}

50%{
	background-position: 100% 0%
}

100%{
	background-position: 0% 100%
}

}







.con{
	
	width: 85%;
    padding: 10px;
    overflow: hidden;
    border: 3px solid #4caf50;
    margin: 10px;
    border-radius: 30px;
    outline: 0px;

}

.con input{
	border: none;
	outline: none;
	background: none;
	color: yellow;
	font-size: 20px;
	float: left;
	margin: 10px;

}

.btn{
	box-shadow: 0px 0px 10px #4caf50;
    width: 90%;
    background: none;
    position: relative;
    top: 26px;
    color: #ffffff;
    font-size: 26px;
    margin: 12px;
    font-weight: 800;
}

.btn:hover{
	z-index: 1;
	transition: 0.2s;
	transform: scale(1.05);
	box-shadow: 0px 0px 10px red;


}
@media(max-width:1300px){
    .form{
        height: 40%;
        position: relative;
        overflow: hidden;
        box-shadow: 2px 2px 10px saddlebrown;
        padding: 5px;
        width: 50%;
        text-align: center;
        text-align: -webkit-center;
        left: 25%;
        top: 0;
    }
}