body{
	padding: 0;
	margin: 0;

	background-size: cover;
	background-repeat: no-repeat;
	background-attachment: fixed;
  scroll-behavior: smooth;

}
::-webkit-scrollbar{
	display: none;
}



.section{
  position: relative;
    width: 100%;
    height: 100px;
    overflow: hidden;
   
    overflow-x: auto;
    display: flex;
    top: 54px;

}
.second-list{display:none;}

.section::scrollbar{
  width: 0px;

}

.section::-webkit-scrollbar{
  width: 0px;

}
b{

    color: #efff00;

}
button{
  position: relative;
 min-width: 18%;
height: 60px;
  font-size: 13px;
    margin: 15px;
    background-color: red;
    outline: 0;
    border-radius: 10px;
    box-shadow: 4px 4px 4px black;
    font-family: 'Raleway';
    font-weight: 900;
    max-width: auto;
    overflow: hidden;

}


              /*Login Button*/
  .login{
    position: relative;
    min-width: 8%;
    height: 70%;
    font-size: 25px;
    background-color: chocolate;
    outline: 0;
    border-radius: 10px;
    box-shadow: 4px 4px 4px black;
    font-family: 'Raleway';
    overflow: hidden;
    font-weight: 200;
    float: right;
    margin: 0;
    right: 4%;
    top: 15%;
    color: rgb(250, 234, 234);
  }




          

.fa{
	display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    float: right;
    font-size: 35px;
    top: 30%;
    position: relative;
    right: 15px;
}
.nav{
	width: 100%;
    height: 60px;
    background-color: #2d3150;
    position: fixed;
    z-index: 2;
    box-shadow: 2px 7px 10px #000000;
    
}
.accountinfo{
  position: relative;
    float: right;
    display: inline-block;
    right: 6px;
    border-right: double;
    font-size: 16px;
    top: 12px;
    padding: 8px;

}
.accountlogout{
  position: relative;
    float: right;
    display: inline-block;
    right: 3px;
    font-size: 16px;
    /* margin-right: 10px; */
    top: 12px;
    padding: 8px;
    
}
#myInput{
  font-size: 25px;
  position: relative;
  top: 10%;
  text-align: center;
  border-top-left-radius: 15px;
  border-bottom-right-radius: 15px;
  border-top-right-radius: 13px;
  outline: 0;
  width: 20%;
  left: 65%;
  height: 70%;
}
.options{
	font-size: 18px;
    position: absolute;
    left: 0px;
    
    width: 40%;


}
.options li{
    display: inline-flex;
    margin-right: 30px;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
    transition: 0.2s;
    

}
.options li:hover{
	transform: scale(1.1);
	
  
}


.options li a{
	text-decoration: none;
  color:chocolate;
  font-weight: 800;
 
}




.page{
	width: 100%;

	
	
}


.list{
	width: 19%;
	position: fixed;
	background-color: none;
	text-align: center;
	top: 10%;
	overflow: hidden;
  box-shadow: 10px -10px 15px black;
  height: 90%;
}
	

.list h1{
	text-align: center;
	text-transform: uppercase;
	letter-spacing: 1px;
	color: chocolate;
	
}


@keyframes anim{
	0%{color:#c02c8a; }
	20%{color:#4444b8; }
	30%{color:#7b0cb2; }
	40%{color:#e3453c; }
	50%{color:#746097;}
	60%{color:#746097;}
	70%{color:#e3453c;}
	80%{color:#7b0cb2;}
	90%{color:#4444b8;}
	100%{color:#c02c8a;}

}

.information{
	font-size: 20px;
	margin-top: 40px;
}





.Topics{
	width: 100%;
	height: 75px;
	margin-bottom: 10px;
	cursor: pointer;

}
.Topics h3{
	text-transform: uppercase;
	letter-spacing: 2px;
	position: relative;
	top: 20px;
	font-size: 20px;
	color: #b2aecf;
   text-shadow: 0px 7px 7px #5d3c3b;
   transition: 1s;
}
.Topics h3:hover{
	transition: 0.5s;
	transform: scale(1.1);
}

.blog{
  width: 78%;
	text-align: center;
	overflow: hidden;
	float: right;
	position: relative;
	top: 10%;
	background-color:none;
	right: 25px;
  box-shadow: 0px -2px 20px black;
  margin-top: 2%;
  height: 300px;
}

.main-page{
  width: 100%;
  position: absolute;
  height: 100%;
}
.Articles{
	width: 78%;
	text-align: center;
	overflow: hidden;
	float: right;
	position: relative;
	top: 10%;
	background-color:none;
	right: 25px;
  box-shadow: 0px -2px 20px black;

}
.Articles h1 ,.blog h1{
	text-transform: uppercase;
	letter-spacing: 2px;
	color: chocolate;
  text-shadow: 2px -6px 7px black;
}
.card{
	width: 16%;
    height: 250px;
    box-shadow: 2px 2px 10px black;
    display: inline-block;
    margin:20px;
    margin-bottom:45px;
    margin-bottom: 60px;
    background-color: #e2cec6;
    border-radius: 31px;
    border-bottom-left-radius: 0px;
    border-top-right-radius: 0px;
    transition: 0.5s;
}

.card:hover {
	transition: 0.5s;
	transform: scale(1.02);
	
}
.card a{
    text-decoration:none;
}



a h4{
height: 20px;
    position: relative;
    font-size: 13px;
    word-spacing: 3px;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-family: 'Potta One', cursive;
    color: #fefefe;
    text-shadow: 0px 3px 10px black;
    transform: translatey(-15px);
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    display: block;
    overflow: hidden;
}
.card img{
	
	    width: 100%;
    height: 100%;
    border-radius: 31px;
    border-bottom-left-radius: 0px;
    border-top-right-radius: 0px;
}
#cm{
	text-align: center;
}

#comment{
	    height: 260px;
    border: 1px solid;
    
    top: 190%;
    width: 80%;
    font-size: 20px;
    text-align: center;

}
.bar{
	width:95%;
    height: 280px; 
    margin-top: 10px;
    position: relative;
    display: flex;
    overflow-x:auto; 
    top: -20px; 
    text-align: center;
    float: right;

}
.bar::-webkit-scrollbar{
	display: none;
}
.bar .imageBar{
	height: 250px;
	width: 250px;
	object-fit: cover;
	box-shadow: 2px 2px 10px black;
	margin-right: 25px;
}


                                                                                    /*Link for scroll */












.barone{
	width: 78%;
	position: relative;
	height: 400px;
	float: right;
	top: 125px;
	background-color: #e2fdff;
	text-align: center;
	right: 25px;
	overflow: hidden;
}









.slider {
 width: 100%;
    height: 318px;
    position: absolute;
    margin: auto;
    overflow-x: scroll;
    overflow-y: hidden;

}



.slider::-webkit-scrollbar {
  display: none;
}

.slider .slide {
  display: flex;
  position: absolute;
  left: 0;
  transition: 0.3s left ease-in-out;
}
.item{
	width: 200px;
    /* margin-right: 4%; */
    box-shadow: 2px 2px 10px black;
    display: inline-block;
    height: 238px;
    position: relative;
    border-radius: 20px;
    
    
}
.item:hover{
  transform: scale(1.05);
}
.item a{
	text-decoration: none;
	letter-spacing: 1px;
	animation: animate 5s linear infinite;
}

.slider .item {
  margin-right: 15px;
}

.slider .item:last-child {
  margin-right: 0;
}

.ctrl {
  text-align: center;
  margin-top: 5px;
}

.ctrl-btn {
  padding: 20px 20px;
  min-width: 50px;
  background: #53326b;
  border: none;
  font-weight: 600;
  text-align: center;
  cursor: pointer;
  outline: none;

  position: absolute;
  top: 50%;
  margin-top: -27.5px;
}

.ctrl-btn.pro-prev {
  left: 0;
}

.ctrl-btn.pro-next {
  right: 0;
}

.heading{
  word-spacing: 3px;
  letter-spacing: 5px;
  left: 20px;
  text-transform: uppercase;
  animation: animate 5s linear infinite;
}
.pName{
  text-transform: uppercase;
  text-align: center;
  font-size: 15px

}
.image{
  
  width: 100%;
    height: 100%;
    border-radius: 20px;

}





.layer-two{
	width: 98%;
	position: relative;
	height: 600px;
	background-color: #87888c;
	float: right;
	top: 150px;
	right: 1%;
}

.sec{
	width: 21%;
    height: 380px;
    background-color: red;
    display: inline-block;
    position: relative;
    margin-right: 60px;
    text-align: center;
    left: 15%;
    top: 22%;
   /* transform: translate(0px, 0px);*/
    border-radius: 2px 2px 2px 2px;
    transition: 2s linear-infinite;
}

